@import "page__oneitem-rating";
.itemPage {
    font-size: 16px;
    .oneitem__body {
        //background: green;
        width: 100%; }
    .oneitem {
        &__title {
            text-transform: uppercase;
            font-size: 24px;
            margin-bottom: .5em; }
        &__back2list {
            color: #ABABAB;
            float: right; }

        &__gallery {
            float: left;
            width: 50%;
            @include md {
                width: 100%;
                margin-bottom: 2em; } }
        &__gallery-carousel {
            position: relative;
            display: block;
            margin: 0 auto;
            max-height: 550px;
            overflow: hidden;
            .owl-carousel {
                width: calc(100% - 140px);
                margin-left: 120px; }

            .owl-carousel .oneitem__itemPic {
                width: 100%; }

            .owl-carousel .owl-controls .owl-dot {
                background-size: cover;
                margin-top: 10px; }

            .owl-carousel .owl-dots {
                position: absolute;
                top: 0;
                left: -100px;
                width: 95px;
                //height: 100%;
                height: 550px;
                overflow: hidden; }

            .owl-carousel .owl-dot {
              width: 80px;
              height: 80px!important;
              display: block;
              margin-bottom: 5px; }

            .owl-carousel .owl-dot {
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat; }
            .owl-carousel .owl-dot.active {
                border: 2px solid #9e9e9e; } }



        &__desc {
            float: right;
            width: 50%;
            position: relative;
            @include md {
                width: 100%;
                float: left; }
            dl {
                color: #949494;
                margin-bottom: .25em; }
            dt {
                float: left;
                width: 90px; }
            dd {
 }                //padding-left: 100px;
            .oneitem__price {
                color: #949494;
                font-size: 24px;
                margin: 1em 0;
                .bonus {
                    font-size: 16px; } }

            .oneitem__sizes {
                @include clr;
                @import "page__oneitem-sizes";
                &-item {
                    float: left;
                    list-style: none;
                    margin: 0;
                    margin-right: 5px; } }



            .btn {
                border: 1px solid #949494;
                border-radius: 5px;
                font-size: 18px;
                font-weight: normal;
                font-family: Gals;
                //background: #fff;
                padding-left: 40px;
                margin-right: .5em;
                margin-bottom: .5em;
                min-width: 185px;
                transition: transform 500ms cubic-bezier(0.17, 0.67, 0.6, 1.3), background .3s;

                &:active {
                    transform: scale(0.95); }

                &-incart {
                    background: #333 url('../img/basket_white.svg') no-repeat 10px 50%;
                    color: #fff;
                    border: none;
                    &:hover {
                        background-color: #222; } }
                &-buyexpress {
                    background: url('../img/buy-express.svg') no-repeat 10px 50%;
                    color: #333;
                    &:hover {
                        background-color: #eee; } } }

            &-tabs {
                overflow: hidden;
                height: 250px;
                @include md {
                    height: 200px; }
                @include sm {
                    height: initial; } } }

        &__whereBuy {
            margin: 1em 0; }

        &__whereBuy-table {
            border: 1px solid #ccc;
            margin: 1em 0;
            padding: 1em;
            color: #ABABAB;
            font-size: 14px;
            th, td {
                border: 1px solid #ccc;
                padding: .25em .5em; }
            tr:nth-child(odd) {
                background: #EEEEEE; }

            td {
                text-align: center; }
            .place {
                text-align: left; } } }

    .b-opinions {
        .opinion {
            border: 1px solid #ccc;
            margin: 1em 0;
            padding: 1em;
            td {
                border: 1px solid #ccc;
                padding: 1em; }
            &__author {
                width: 300px;
                color: #333333;
                time {
                    color: #ABABAB; } }

            &__text {}
            .authName {}


            dt {
                float: left;
                width: 200px;
                border-right: 1px solid #ccc;
                color: #333333;
                time {
                    color: #ABABAB; } }

            dd {
                //float: left;
                margin-left: 220px; } }
        .btn {
            float: right; } } }
